/* MAIN CONTAINER */
body {
  padding: 1em 1em; }
  body div.main-container {
    position: relative;
    max-width: 915px;
    min-width: 50%;
    margin-left: auto;
    margin-right: auto; }

/* ELEMENT TABLE */
.element-table {
  width: 100%;
  display: table-cell; }
  .element-table .element-table-header {
    font-weight: bold;
    display: inline-block;
    float: right;
    margin-right: 5px; }
  .element-table .element-table-header:first-child {
    float: left; }

.element-row {
  border-width: 2px;
  border-style: solid;
  margin-bottom: 5px;
  vertical-align: middle;
  height: 40px;
  padding: 3px;
  cursor: pointer;
  overflow: hidden;
  -webkit-transition: height 200ms linear;
  -moz-transition: height 200ms linear;
  -o-transition: height 200ms linear;
  -ms-transition: height 200ms linear;
  transition: height 200ms linear; }
  .element-row.selected {
    height: 200px; }
  .element-row.running {
    border-color: #4B85B7;
    color: #4B85B7;
    background-image: linear-gradient(45deg, #cce0f1, white); }
  .element-row.pending {
    border-color: silver;
    color: silver;
    background-image: linear-gradient(45deg, #efefef, white); }
  .element-row.rejected {
    border-color: #BC0008;
    color: #BC0008;
    background-image: linear-gradient(45deg, #ffdfe0, white); }
  .element-row.complete, .element-row.accepted {
    border-color: #61953F;
    color: #61953F;
    background-image: linear-gradient(45deg, #ebf9e2, white); }

.element-cell {
  padding-right: 10px;
  display: inline-block; }

.element-row .loader-container {
  text-align: center;
  padding-top: 30px;
  padding-left: 45%; }
  .element-row .loader-container .loader {
    border: 16px solid #ffffff;
    /* Light grey */
    border-top: 16px solid #3498db;
    /* Blue */
    border-radius: 50%;
    width: 80px;
    height: 80px;
    animation: spin 2s linear infinite; }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

/* PROGRESS BOX */
.progress-box {
  margin: 1px;
  width: 30px;
  height: 20px;
  border: 1px solid gray;
  background-color: #DADADA;
  position: relative;
  margin-top: 5px;
  margin-bottom: -5px; }
  .progress-box .progress-inner {
    content: '\A';
    position: absolute;
    height: 100%;
    top: 0;
    bottom: 0;
    left: 0; }
    .progress-box .progress-inner.running {
      background: #4B85B7; }
    .progress-box .progress-inner.pending {
      background: silver; }
    .progress-box .progress-inner.rejected {
      background: #BC0008; }
    .progress-box .progress-inner.accepted, .progress-box .progress-inner.complete {
      background: #61953F; }

/* DETAIL BOX */
.detail-box {
  width: 150px;
  height: 150px;
  border-width: 2px;
  border-style: solid;
  margin: 5px;
  padding: 3px;
  display: inline-table;
  background-color: white;
  cursor: pointer;
  font-size: 0.8em; }
  .detail-box:hover {
    border-style: dashed; }
  .detail-box.rejected {
    border-color: #BC0008;
    color: #BC0008; }
  .detail-box.complete {
    border-color: #61953F;
    color: #61953F; }
  .detail-box .computer {
    display: table-cell;
    text-align: center; }
  .detail-box .footer {
    display: table-footer-group;
    width: 100%;
    text-align: center;
    color: black; }
  .detail-box .metric-item {
    width: 40px;
    height: 40px;
    margin: 10px;
    margin-top: 5px;
    color: black;
    text-align: center;
    display: inline-block; }
  .detail-box .metric-arrow {
    width: 100%;
    height: 100%;
    padding: 10px; }
  .detail-box .metric-value {
    margin-top: -20px;
    position: relative;
    color: white; }
  .detail-box .metric-label {
    margin-top: 8px; }
  .detail-box .box-title {
    font-size: 1.2em;
    font-weight: bold;
    display: block; }
  .detail-box .detail-graph {
    float: left;
    margin-left: -30px;
    margin-right: -30px;
    margin-top: -10px; }
  .detail-box .detail-percent {
    font-size: 1.5em;
    margin-top: 20px;
    text-align: center; }
    .detail-box .detail-percent.green {
      color: green; }
    .detail-box .detail-percent.yellow {
      color: #9D9D62; }
    .detail-box .detail-percent .label {
      font-size: 0.6em;
      font-weight: normal;
      color: black;
      display: flex; }
  .detail-box .rotate90 {
    -ms-transform: rotate(90deg);
    /* IE 9 */
    -webkit-transform: rotate(90deg);
    /* Chrome, Safari, Opera */
    transform: rotate(90deg); }
  .detail-box .rotate180 {
    -ms-transform: rotate(180deg);
    /* IE 9 */
    -webkit-transform: rotate(180deg);
    /* Chrome, Safari, Opera */
    transform: rotate(180deg); }
  .detail-box .detail-progress {
    display: table-row;
    height: 30px; }
    .detail-box .detail-progress span {
      position: absolute;
      color: #000000;
      margin-top: 5px;
      font-size: 0.9em;
      line-height: 1em; }

/* RESULT BOX */
.result-box {
  width: 150px;
  height: 150px;
  margin: 5px;
  padding: 3px;
  display: inline-table;
  text-align: center; }
  .result-box .result {
    font-size: 1.6em;
    font-weight: bold; }

.result-label {
  color: black;
  display: block; }

.result-action {
  margin-bottom: 0; }

/* MODAL */
.modal-header {
  background: #0480be;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px; }
